@font-face {
    font-family: "Quite Chocolatey";
    src: url(../fonts/QuiteChocolatey.ttf); 
   }

@import url('https://fonts.googleapis.com/css?family=Montserrat');
@import url('https://fonts.googleapis.com/css?family=PT+Serif');

body {
    background-color: #e3d5b8;
}

.header {
    background-image: url(../images/fon.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    width: 1200px;
    margin: 0 auto;

        .fon {
            background-color: rgba(227, 213, 184, 0.5);

            .header_tittle {    
                width: 993px;
                margin: 0 auto;
                display: flex;
                justify-content: space-between;
                align-items: flex-start;
                padding-bottom: 150px;

                .logo{
                    padding-top: 38px;
                    padding-bottom: 23px;   
                    display: block;
                    color: #d03958;
                    font-family: "Quite Chocolatey";
                    font-size: 88px;
                    font-weight: 400;
                    line-height: 32.38px;
                    letter-spacing: -1.2px;
                }
                .navigation {
                    padding-top: 30px;
                    padding-bottom: 30px;
                    padding-left: 30px;

                    .header_menu {
                        display: flex;

                        .menu_hover {
                            text-decoration: none;
                            padding: 12px 21px;
                            color: #1b1b1b;
                            font-family: 'Montserrat', sans-serif;
                            font-size: 18px;
                            font-weight: 400;
                            line-height: 22px;
                            text-transform: uppercase;
                            letter-spacing: -0.36px;
                        }

                        .menu_hover:hover {
                            background-color: #d03958;
                            border-radius: 5px;
                            color: #ffffff;
                        }
                    }
                }
            }

            .header_text_all {
                width: 993px;
                margin: 0 auto;
                display: flex;
                flex-direction: row;
                justify-content: space-between;

                .header_text {
                    max-width: 493px;
     
                    h1 {
                        color: #d03958;
                        font-family: 'Montserrat', sans-serif;
                        font-size: 90px;
                        font-weight: 700;
                        line-height: 90px;
                        text-transform: uppercase;
                        letter-spacing: -1.8px;
                        padding-bottom: 47px;

                        span {
                            color: #161616;
                        }
                    }

                    p {

                        color: #161616;
                        font-family: 'PT Serif', serif;
                        font-size: 20px;
                        font-weight: 400;
                        line-height: 28px;
                        padding-bottom: 156px;
                    }
                }

                .header_form {
                    background-color: #d03958;
                    border-radius: 5px;
                    padding-left: 24px;
                    padding-right: 24px;
                    position: relative;
                    top: 81px;

                    .form_text {
                        color: #ffffff;
                        font-family: 'Montserrat', sans-serif;
                        font-size: 22px;
                        font-weight: 400;
                        text-transform: uppercase;
                        letter-spacing: -0.44px;
                        padding-bottom: 35px;
                        padding-top: 35px;
                        display: block;
                        text-align: center;
                    }

                    .form_input {
                        background-color: #ffffff;
                        border-radius: 3px;
                        margin-bottom: 13px;
                        width: 356px;
                        padding-left: 3px;

                        .user {
                            outline:none;
                            padding: 21px;
                            border: 0;
                            width: 310px;
                        }

                        select {
                            -webkit-appearance: none;
                            -moz-appearance: none;
                            appearance: none;
                        }
                    }

                    .checkbox {
                        color: #ffffff;
                        font-family: 'PT Serif', serif;
                        font-size: 20px;
                        font-weight: 400;
                        line-height: 25px;
                        letter-spacing: -0.4px;
                    }
                }

                .submit {
                    background-color: #161616;
                    border-radius: 3px;
                    margin-top: 18px;
                    margin-bottom: 28px;
                    width: 356px;
                    padding-top: 22px;
                    padding-bottom: 22px;
                    color: #ffffff;
                    font-family: 'Montserrat', sans-serif;
                    font-size: 22px;
                    font-weight: 400;
                    text-transform: uppercase;
                    letter-spacing: -0.44px;
                    border: 0;
                }
            }
        }
    }

.main_all {
    background-color: #fff;

    .main {
        margin: 0 auto;
        width: 995px;

        .main_tittle {
            color: #161616;
            font-family: 'Montserrat', sans-serif;
            font-size: 50px;
            font-weight: 700;
            line-height: 80px;
            text-transform: uppercase;
            letter-spacing: -1px;
            padding-bottom: 91px;

            span {
                color: #d03958;
            }
        }

        .main_cards {
            display: flex;
            flex-direction: row;
            justify-content: space-between;

            .item {
                display: flex;
                flex-direction: column;
                align-items: center;
                max-width: 174px;

                .card {
                    background-image: url('../images/1.png');
                    width: 174px;
                    height: 157px;
                    background-repeat: no-repeat;
                    background-position: center center;
                }

                .card2 {
                    background-image: url('../images/2.png');
                }

                .card3 {
                    background-image: url('../images/3.png');
                }

                .card4 {
                    background-image: url('../images/4.png');
                }

                .card_text {
                    padding-top: 43px;
                    text-align: center;
                    padding-bottom: 66px;
                    color: #161616;
                    font-family: 'PT Serif', serif;
                    font-size: 20px;
                    font-weight: 400;
                    line-height: 28px;
                    letter-spacing: -0.4px;
                }
            }
        }
    }
}

.content_all {
    background-color: #d03958;

    .content{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        padding-top: 26px;
        padding-bottom: 43px;
        margin: 0 auto;
        width: 992px;
        padding-right: 5px;
        padding-left: 3px;

        .content_text {
            color: #ffffff;
            font-family: 'PT Serif', serif;
            font-size: 20px;
            font-weight: 400;
            line-height: 28px;
            letter-spacing: -0.4px;
            width: 601px;
        }

        .content_button {
            display: block;
            text-align: center;
            text-decoration: none;
            padding-top: 25px; 
            padding-bottom: 25px;
            border: 1px solid #e3d5b8;
            border-radius: 5px;
            background-color: #e3d5b8;
            width: 272px;
            color: #161616;
            font-family: 'Montserrat', sans-serif;
            font-size: 22px;
            font-weight: 400;
            line-height: 28px;
            text-transform: uppercase;
            letter-spacing: -0.44px;
        }
    }
}

.price_all {

    .price {
        padding-top: 60px;
        width: 997px;
        margin: 0 auto;
        
        .price_tittle {
            color: #1f1f1f;
            text-align: center;
            
            h1 {
                font-family: 'Montserrat', sans-serif;
                font-size: 50px;
                font-weight: 700;
                line-height: 80px;
                text-transform: uppercase;
                letter-spacing: -1px;
                padding-bottom: 55px;
            }
        }

        .price_block_all{
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            position: relative;
            
        .price_block{
            width: 321px;
            background-color: #ffffff;
            display: flex;
            flex-direction:column;
            justify-content: center;
            align-items: center;
            border-radius: 5px;

            h3 {
                padding-top: 57px;
                color: #161616;
                font-family: 'PT Serif', serif;
                font-size: 22px;
                font-weight: 400;
                line-height: 28px;
                letter-spacing: -0.44px;
            }

            p {
                padding-top: 30px;
                padding-bottom: 33px;
                color: #818b95;
                font-family: Lato;
                line-height: 22px;
                font-variant: small-caps;
                color: #ffbb42;
                font-size: 60px;
                font-weight: 700;
                text-transform: uppercase;

                span {
                    color: #d03958;
                    font-family: 'Montserrat', sans-serif;
                    font-size: 60px;
                    font-weight: 700;
                    line-height: 80px;
                    text-transform: uppercase;
                    letter-spacing: -1.2px;
                }
            }
            .price_account{
                max-width: 329px;
                padding-left: 31px;
                padding-right: 31px;
                color: #b5bbc1;
                font-family: Lato;
                font-size: 14px;
                font-weight: 400;
                line-height: 15px;
                text-transform: uppercase;
                padding-bottom: 40px;
                
                .span_1 {
                    display: block;
                    color: #161616;
                    font-family: 'PT Serif', serif;
                    font-size: 20px;
                    font-weight: 400;
                    line-height: 28px;
                    letter-spacing: -0.4px;
                    text-align: center;
                    text-transform: none;
                }
            }

            .price_button {
                text-decoration: none;
                padding-top: 20px;
                padding-bottom: 16px;
                background-color: #e3d5b8;
                border-radius: 3px;
                color: #161616;
                font-family: 'Montserrat', sans-serif;
                font-size: 22px;
                font-weight: 400;
                line-height: 28px;
                text-transform: uppercase;
                letter-spacing: -0.44px;
                width: 208px;
                display: flex;
                flex-direction: row;
                justify-content: center;
            }

            .price_button:hover {
                background-color: #d03958;
                color: #fff;
            }
        }

            .price_link::before {
                content: url('../images/2-layers.png');
                position: absolute;
                top: -1px;
                left: 337px;  
            }
        }
    }
}

.user_container_all{
    background-color: #fff;
        
    .user_container {
        margin: 0 auto;
        width: 1014px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding-top: 150px;
        padding-bottom: 116px;

        .user_test {

            .test_tittle {
                color: #1f1f1f;
                font-family: 'Montserrat', sans-serif;
                font-size: 50px;
                font-weight: 700;
                line-height: 80px;
                text-transform: uppercase;
                letter-spacing: -1px;
                display: flex;
                justify-content: center;
                padding-bottom: 32px;
            }

            .test_content {
               
                .test_text {
                    position: relative;
                    padding-bottom: 32px;
                    
                    .test_text_2 {
                        background-color: #e3d5b8;
                        border-radius: 5px;
                        width: 412px;
                        padding: 28px 0 28px 13px;
                        position: relative;
                        left: -15px;
                   
                    p {
                        padding-bottom: 30px;
                        color: #161616;
                        font-family: 'PT Serif', serif;
                        font-size: 20px;
                        font-weight: 400;
                        line-height: 28px;
                        letter-spacing: -0.4px;
                        max-width: 336px;
                    }

                    span {
                        color: #d03958;
                        font-family: 'Montserrat', sans-serif;
                        font-size: 20px;
                        font-weight: 400;
                        line-height: 28px;
                        letter-spacing: -0.4px;
                        display: block;
                    }
                }

                .text_color {
                    background-color: #fff;
                }
            }

            .test_text::after {
                content: url('../images/layer-4.png');
                width: 107px;
                height: 107px;
                position: absolute;
                left: 350px;
                top: 30px;
            } 

            .icon_ava::after {
                content: url('../images/layer-6.png');
            }     
        } 
    }

    .user_gallery{

        .test_tittle {
            color: #d03958;
            font-family: 'Montserrat', sans-serif;
            font-size: 50px;
            font-weight: 700;
            line-height: 80px;
            text-transform: uppercase;
            letter-spacing: -1px;
            padding-bottom: 32px;
            display: flex;
            justify-content: center;
        }

        .gallery {
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            max-width: 386px;

            .gallery_img {
                padding-bottom: 28px;
            }
        }

        .view_gallery {
                padding-top: 17px;
                color: #d03958;
                font-family: 'Montserrat', sans-serif;
                font-size: 20px;
                font-weight: 400;
                line-height: 28px;
                text-decoration: underline;
                text-transform: uppercase;
                letter-spacing: -0.4px;
                display: flex;
                justify-content: center;

                a:visited {
                    color: #d03958;
                }
            }
        }
    }
}

.footer_all {
    background-color: #161616;

    .footer {
        margin: 0 auto;
        width: 1004px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        .footer_text {
                color: #ffffff;
                font-family: 'PT Serif', serif;
                font-size: 20px;
                font-weight: 400;
                line-height: 22px;
                letter-spacing: -0.4px;
                padding-top: 82px;
                padding-bottom: 84px;

            a {
                color: #d03958;
            }

            a:visited {
                color: #d03958;
            }
        }
        
        .logo {
            padding-top: 94px;
            padding-bottom: 64px;   
            display: block;
            color: #d03958;
            font-family: "Quite Chocolatey";
            font-size: 88px;
            font-weight: 400;
            line-height: 32.38px;
            letter-spacing: -1.2px;
        }
    }
}